.skill_image_container {
  text-align: center;
  display: inline-block;
  width: 114px;
  height: 116px;
  border-radius: 30px 30px;
  background-size: cover;
  background-repeat: no-repeat;
  margin-right: 2rem;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2), 0 3px 6px rgba(0, 0, 0, 0.2);
}
.skill_label {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: black;
  border-radius: 30px 30px;
  width: 114px;
  height: 116px;
  padding: 10px;
  opacity: 0.8;
  font-weight: 400;
  font-size: 1rem;
  color: white;
  visibility: hidden;
}

.skill {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  background: rgb(231, 230, 230);
  padding-top: 8vh;
  padding-bottom: 4vh;
}
.skill_title {
  font-family: 'Noto Sans KR', sans-serif;
  text-align: center;
  -webkit-align-self: center;
          align-self: center;
  font-size: 4vw;
  margin-bottom: 30px;
}

.skill_Content {
  margin-bottom: 20px;
}

.skill_image_container:hover .skill_label {
  visibility: visible;
  animation: fadeout1 0.5s;
  -moz-animation: fadeout1 0.5s; /* Firefox */
  -webkit-animation: fadeout1 0.5s; /* Safari and Chrome */
  -o-animation: fadeout1 0.5s; /* Opera */
}
@keyframes fadeout1 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}
@-webkit-keyframes fadeout1 {
  /* Safari and Chrome */
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}

